section {
  margin-bottom: 40px;
}

.intro-section {
  text-align: center;
}

.intro-section h1 {
  font-size: 2.5em;
  margin-bottom: 10px;
}

.intro-section p {
  font-size: 1.2em;
  margin-bottom: 20px;
}

.profile-photo {
  border-radius: 50%;
  width: 150px;
  height: 150px;
  object-fit: cover;
  margin-top: 20px;
}

.two-columns {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.column {
  width: 48%;
}

.column-small {
  width: 44%;
}

.column-big {
  width: 54%;
}

.column-small1 {
  width: 40%;
}

.column-big1 {
  width: 55%;
}

@media (max-width: 768px) {
  .column {
    width: 100%;
    margin-bottom: 20px;
  }

  .column-small,
  .column-small1 {
    width: 100%;
    margin-bottom: 20px;
  }

  .column-big,
  .column-big1 {
    width: 100%;
    margin-bottom: 20px;
  }
}

.highlights-section h2 {
  font-size: 2em;
  margin-bottom: 10px;
}

.highlights-section p {
  font-size: 1.1em;
  margin-bottom: 5px;
}

.navigation-prompts h2 {
  font-size: 2em;
  margin-bottom: 10px;
}

.navigation-prompts ul {
  list-style: none;
  padding: 0;
}

.navigation-prompts li {
  margin-bottom: 20px;
}

.navigation-prompts a {
  font-size: 1.2em;
  color: #0066cc;
  text-decoration: none;
}

.navigation-prompts a:hover {
  text-decoration: underline;
}

.featured-project h2 {
  font-size: 2em;
  margin-bottom: 10px;
}

.featured-project p {
  font-size: 1.1em;
  margin-bottom: 10px;
}

.featured-project a {
  font-size: 1.2em;
  color: #0066cc;
  text-decoration: none;
}

.featured-project a:hover {
  text-decoration: underline;
}

.call-to-action h2 {
  font-size: 2em;
  margin-bottom: 10px;
}

.call-to-action p {
  font-size: 1.2em;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes bounceIn {

  0%,
  20%,
  40%,
  60%,
  80%,
  100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.construction-banner {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px;
  background: #f9f9f9; /* Light grey background for text contrast */
  border: 10px solid transparent; /* Create space for the border */
  border-image: url('/caution_tape.png') 30 round; /* Apply the caution tape image */
  border-radius: 5px;
  margin-bottom: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Slight shadow for depth */
  justify-content: center;
}

.construction-icon {
  font-size: 2rem;
  color: #ff9800; /* Match the construction theme */
}

.construction-banner h2 {
  margin: 0;
  font-size: 1.5rem;
  color: #333; /* Darker text color for readability */
}
