.container {
  position: fixed;
  /* position: absolute; */
  top: 30px;
  /* Distance from the top of the viewport */
  right: 30px;
  /* Distance from the right of the viewport */
  z-index: 999;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  /* color: red; */
}

@media only screen and (max-width: 768px) {
  .container {
    /* position: fixed;
      top: 25px;
      right: 5px; */
    z-index: 999;
    /* Ensure the button is above other elements */
    padding: 8px 16px;
    border-radius: 4px;
    cursor: pointer;
    position: inherit;
    /* background-color: black; */
  }
}

.container input {
  display: none;
}

.container input+div {
  border-radius: 50%;
  width: 36px;
  height: 36px;
  position: relative;
  box-shadow: inset 14px -14px 0 0 var(--text-color, #000);
  transform: scale(1) rotate(-2deg);
  transition: box-shadow 0.5s ease 0s, transform 0.4s ease 0.1s;
}

.container input+div::before {
  content: "";
  width: inherit;
  height: inherit;
  border-radius: inherit;
  position: absolute;
  left: 0;
  top: 0;
  transition: background 0.3s ease;
}

.container input+div::after {
  content: "";
  width: 8px;
  height: 8px;
  border-radius: 50%;
  margin: -4px 0 0 -4px;
  position: absolute;
  top: 50%;
  left: 50%;
  box-shadow: 0 -23px 0 var(--text-color, #eee),
    0 23px 0 var(--text-color, #eee),
    23px 0 0 var(--text-color, #eee),
    -23px 0 0 var(--text-color, #eee),
    15px 15px 0 var(--text-color, #eee),
    -15px 15px 0 var(--text-color, #eee),
    15px -15px 0 var(--text-color, #eee),
    -15px -15px 0 var(--text-color, #eee);
  transform: scale(0);
  transition: all 0.3s ease;
}

.container input:checked+div {
  box-shadow: inset 32px -32px 0 0 #fff;
  transform: scale(0.5) rotate(0deg);
  transition: transform 0.3s ease 0.1s, box-shadow 0.2s ease 0s;
}

.container input:checked+div::before {
  background: var(--text-color, #eee);
  transition: background 0.5s ease 0.5s;
}

.container input:checked+div::after {
  transform: scale(1.5);
  transition: transform 0.5s ease 0.15s;
}