.app {
  position: relative;
}

.top-bar {
  display: flex;
  justify-content: flex-end;
  padding: 10px;
  position: fixed;
  top: 80px;
  right: 35px;
  z-index: 1000;
  /* Ensure top-bar is above the sidebar */
}

.accessibilityIcon {
  margin-left: 10px;
  cursor: pointer;
  position: relative;
  font-size: 35px;
  z-index: 1000;
  /* Ensure the icon is above the sidebar */
  color: var(--text-color);
}

.popup {
  position: fixed;
  top: 15%;
  right: 5%;
  width: 250px;
  background-color: var(--text-color-active);
  padding: 20px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
  transform: scale(0);
  transform-origin: top right;
  transition: transform 0.3s ease-in-out;
  z-index: 1000;
  border-radius: 8px;
  border: var(--border);
}

.popup.open {
  transform: scale(1);
}

.sidebar {
  width: 230px;
  position: fixed;
  top: 0;
  right: 0;
  height: 100%;
  background-color: var(--text-color-active);
  padding: 60px 20px 20px 20px;
  /* Add padding to avoid overlap */
  box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.5);
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
  z-index: 999;
  border: var(--border);

  /* Lower z-index than top-bar and accessibilityIcon */
}

.sidebar.open {
  transform: translateX(0);
}

.setting {
  display: block;
  padding-top: 20px;
  color: var(--background-color);
}

.toggle-theme p {
  margin: 10px 0 0;
  font-size: 14px;
  color: var(--background-color);
}

.font-size-controls {
  display: flex;
  align-items: center;
  gap: 10px;
}

.font-size-controls button {
  background-color: var(--background-color);
  color: var(--text-color-active);
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  font-size: 16px;
  border-radius: 3px;
}

.font-size-controls span {
  font-size: 16px;
  color: var(--background-color);
}

.reset-button {
  display: block;
  margin-top: 20px;
  padding: 10px;
  background-color: #f5f5f5;
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
  text-align: center;
  color: #333;
}

.reset-button:hover {
  background-color: #e0e0e0;
}
.high-contrast-setting {
  display: flex;
  align-items: center;
}

.custom-checkbox {
  width: 24px;
  height: 24px;
  background-color: var(--background-color); /* Default background color */
  /* border: 2px solid #fff; */
  
  border-radius: 3px; /* Rounded corners */
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin-left: 10px;
  position: relative;
}

.custom-checkbox::before {

  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  /* background-color: red; */
  border-radius: 3px;
  transition: background-color 0.3s;
}

.custom-checkbox.checked::before {
  background-color: var(--background-color); /* Checked background color */
}

.custom-checkbox.checked::after {
  content: '✓'; /* Checkmark */
  font-size: 16px;
  color: var(--text-color-active); /* Checkmark color */
  position: absolute;
}

.custom-checkbox::after {
  content: '✗'; /* Cross mark */
  font-size: 16px;
  color: var(--text-color-active); /* Cross mark color */
  position: absolute;
}

@media only screen and (max-width: 768px) {
  .top-bar {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    justify-content: flex-end;
  }

  .top-bar> :not(.accessibilityIcon) {
    display: none;
  }

  .accessibilityIcon {
    display: block;
    margin-right: 20px;
    /* Adjust as needed */
    margin-top: 20px;
    font-size: 35px;
  }

  .popup {
    display: none;
  }

  .sidebar {
    top: 0;
    /* Ensure sidebar starts from the top */
    padding-top: 60px;
    /* Adjust padding to account for the top bar height */
  }

  .toggle-theme {
    display: flex;
    align-items: center;
    /* gap: 10px; */
  }

  .setting {
    /* width: 100%; */
    text-align: left;
    /* margin-bottom: 10px; */
  }

  
}

