/* Resume Side Navigation Styles */
.resume-side-nav {
    position: sticky;
    top: 70px;
    left: 50px;
    width: 200px;
    background-color: var(--text-color-active);
    padding-left: 2%;
    box-shadow: 2px 0 4px rgba(0, 0, 0, 0.1);
    z-index: 1000;
    float: left;
    border: var(--border);
    border-radius: 8px;
}

.resume-side-nav ul {
    list-style: none;
    padding: 0;
}

.resume-side-nav li {
    margin-bottom: 15px;
}

.resume-side-nav li a {
    text-decoration: none;
    color: var(--text-color);
    font-weight: bold;
    cursor: pointer;
}

.resume-side-nav li a:hover {
    color: #007bff;
}

.resume-side-nav li.active a {
    color: #007bff; /* Change color of active link */
}

