  align-items: center;



}
.dark-theme.high-contrast {

  --background-color: white;
  --text-color-active: black;
  --text-color: white;
  --filter: brightness(200%);
  --page-container-background: black;
  --border: 1px solid white;
  --border-active: 1px solid black;
  --logo-filter: drop-shadow(1px 1px 0 white)
  drop-shadow(-1px -1px 0 white)
  brightness(110%);
  --nav-background-color: black;
  --carousel-item-background: black;

}
.light-theme.high-contrast {

  --background-color: black;
  --text-color-active: white;
  --text-color: black;
  --filter: contrast(150%);
  --page-container-background: white;
  --border: 1px solid black;
  --border-active: 1px solid white;
  --logo-filter: drop-shadow(1px 1px 0 black)
  drop-shadow(-1px -1px 0 black);
  --nav-background-color: white;
  --carousel-item-background: white;
  
}

