.navbar {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  font-weight: bold;
}

.subnav {
  display: none; /* Hide subnav by default */
  position: absolute; /* Position it relative to the parent li */
  top: 100%; /* Position it below the parent li */
  left: 0;
  background-color: rgba(128, 128, 128, 0.499);
  padding: 10px 0;
  border-radius: 8px;
  box-shadow: 0 8px 16px rgba(0,0,0,0.2); /* Optional: Add shadow for better visibility */
  
}

.subnav li {
  position: relative;
  display: inline-block;
  margin: 0; /* Reset margin */
  
}

.subnav a {
  display: inline-block;
  padding: 10px 20px; /* Same padding as main nav */
  white-space: nowrap; /* Prevent text wrapping */
  
}

.navbar li:hover .subnav {
  display: block;
}

.menu-toggle {
  display: none;
}

