/* Wrapper for the carousel and controls */
.carousel-wrapper {
  position: relative; /* Allows absolute positioning of child elements */
  width: 100%; /* Ensures the wrapper takes up the full width */
  max-height: fit-content;
  margin: auto; /* Centers the carousel horizontally */
  /* overflow: hidden; */
  /* padding-left: 50px; */
  /* padding-right: 50px; */

}



/* Carousel Controls Container */
.carousel-controls {
  position: absolute; /* Positions controls relative to the carousel-wrapper */
  top: 50%; /* Vertically centers the controls */
  width: 100%; /* Ensures controls span the full width of the carousel */
  display: flex;
  justify-content: space-between; /* Positions buttons at each end */
  transform: translateY(-50%); /* Adjusts for vertical centering */
  pointer-events: none; /* Makes sure that the buttons don’t block the carousel */
  /* padding: 0 20px; */
}


.carousel-btn {
  background-color: white;
  color: black;
  border: 2px solid black;
  border-radius: 50%; /* Makes the buttons circular */
  width: 20px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px; /* Adjust size as needed */
  cursor: pointer; /* Changes cursor to pointer on hover */
  transition: background-color 0.3s ease, color 0.3s ease, transform 0.3s ease; /* Smooth transition for hover effects */
  pointer-events: all; /* Ensures the buttons are clickable */
  /* margin: 100px; */
  /* z-index: 1007; */

}

.carousel-btn:hover {
  background-color: black; /* Changes background color on hover */
  color: white; /* Changes text color on hover */
  transform: scale(1.1); /* Slightly enlarges the button on hover */
}

.carousel-btn:focus {
  outline: none; /* Removes the default outline */
}

